 @import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');
 
* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --background-color: #1f1f38;
    --background-color-alt: #2c2c6c;
    --main-color: #4db5ff;
    --main-color-variant: rgba(77, 181, 255, 0.4);
    --white: #fff;
    --beige: rgba(255, 255, 255, 0.6);

    transition: all 400ms ease;

    --wrapper-large: 75%;
    --wrapper-medium: 86%;
    --wrapper-small: 90%;

}

html {
    scroll-behavior: smooth;  
}

::-webkit-scrollbar {
    display: none;
}

body {
    font-family: 'Poppins', sans-serif;
    background: var(--background-color);
    color: var(--white);
    line-height: 1.7;
    background-image: url(../src/assets/bg-texture.png);

}

/* General Styles */

.container{
    width: var(--wrapper-large);
    margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

section {
    margin-top: 8rem;
}

section > h2,
section > h5 {
    text-align: center;
    color: var(--beige);
}

section > h2 {
    color: var(--main-color);
    margin-bottom: 3rem;
}

.text-light {
    color: var(--beige);
}

a {
    color: var(--main-color);
    transition: var(--transition);
}

a:hover {
    color: var(--white);
}

.btn {
    width: max-content;
    display: inline-block;
    color: var(--main-color);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--main-color);
    transition: var(--transition);
}

.btn-primary {
    background: var(--main-color);
    color: var(--background-color);
}

img {
    display: block;
    width: 100%;
    object-fit:cover;
}

/* Media Query (medium devices) */
@media screen and (max-width: 1024px) {
    .container {
        width: var(--wrapper-medium)
    }
}

section {
     margin-top: 6rem;
}

/* Media Queries small devices */
@media screen and (max-width: 600px) {
    .container {
        width: var(--wrapper-small)
    }

    section > h2 {
        margin-bottom: 2rem;
    }
}