header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header-wrapper {
    text-align: center;
    height: 100%;
    position: relative;
}


.call-to-action {
     margin-top: 2.5rem;
     display: flex;
     gap: 1.2rem;
     justify-content: center;
}

.header-social-media {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header-social-media::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--main-color);
}

.profile-picture {
    background: linear-gradient(var
    (--main-color), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
} 

.scroller {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* Media Query (medium devices) */
@media screen and (max-width: 1024px) {
    header {
        height: 68vh;
    }
    }


/* Media Query (small devices) */
@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }

    .header-social-media,
    .scroller {
        display: none;
    }
}