.exp-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
}

.exp-wrapper > div {
    background: var(--background-color-alt);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.exp-wrapper > div:hover {
    background: transparent;
    border-color: var(--main-color-variant);
    cursor: default;
}

.exp-wrapper > div h3 {
    text-align: center;
    margin-bottom: 2rem;
    color: var(--main-color);
}

.exp-content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 2rem;
}

.exp-description {
    display: flex;
    gap: 1rem;
}

.exp-description-icon {
    margin-top: 6px;
    color: var(--main-color);
}

@media screen and (max-width: 1024px) {
    .exp-wrapper {
        grid-template-columns: 1fr;
    }

    .exp-wrapper > div {
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }

    .exp-content {
        padding: 1rem;
    }
}



@media screen and (max-width: 600px) {
    .exp-wrapper {
        gap: 1rem;
    }

    .exp-wrapper > div {
        width: 100%;
        padding: 2rem 1rem;
    }
}